<template>
    <v-col cols="12" sm="10" md="8" xl="4" xxl="4" >
        <v-sheet class="mx-auto rounded" width="100%">
            <v-form fast-fail @submit.prevent>
                <v-text-field hide-details="auto" label="Co jest do zrobienia ?" class="input" type="text" v-model="newTodo" @keyup.enter="addTodo" full-width></v-text-field>
                <v-btn type="submit" class="mt-2 btnH" block @click="addTodo">DODAJ</v-btn>
                <p class="errorLine" v-if="errorMsg">{{ errorMsg }}</p>
            </v-form> 
        </v-sheet>
    </v-col>    
</template>
<script>
export default {
data(){
    return{
        newTodo: '',
        errorMsg: ''
    }
},
methods: {
    addTodo(){
        if (this.newTodo == ''){ //sprawdzamy czy nowy todos nie jest pustym stringiem
            this.errorMsg = 'Jesteś debilem ?' 
        } else {
            this.$emit('add-todo', this.newTodo.trim());// emitujemy zdarzenie o nazwie add-todo(takiej jak nasłuchujemy w app), i przekazujemy wartość tekstową newTodo
            this.errorMsg = '' //czyscimy msg w przypadku gdy ktos poprzednio nie podal zadnego zadania
        }
        this.newTodo= ''
    }
}
}
</script>

<style scoped>
.input{
    width: 100%;
    font-size: 20px;
    border-radius: 8px;
}
.add-button{
    font-size: 20px;
    margin-left: 2em;
    border-radius: 5px;
}
.errorLine{
    color: red;
    font-size: 18px;
    margin-top: 1em;
}
.v-btn:hover{
    background-color: #BA68C8;
}
</style>