<template>
    <v-row dense class="pt-6 d-flex align-center justify-center">
        <v-col cols="12" sm="10" md="8" xl="4" xxl="4" >
            

                <v-card fluid v-bind="props" variant='tonal' class="rounded d-flex flex-no-wrap justify-space-between align-center pa-4 text-uppercase" width="100%" :class="{'todoDoneBck':styleState}">

                   <div class="todo-text" :class="{'todoDone':styleState}">{{ todo.text }}</div> 
                

                <div class="todo-tools d-flex">
                    <v-btn class="todo-tools__item x mobile-spacer" @click="$emit('remove-todo', todo.id)">X</v-btn>
                    <v-btn class="todo-tools__item edit mobile-spacer" @click="$emit('edit-todo', state, todo.id)">EDIT</v-btn>
                    <v-btn class="todo-tools__item done mobile-spacer" @click="doneTodo">DONE</v-btn>    
                </div>
        
        
                </v-card>
            
        </v-col>    
    </v-row>        
</template>
<script>
export default{
    data(){
        return{
            styleState: '',
            state: false
        }
    },
    props:['todo'],
       
    methods: {
        doneTodo(){
            this.styleState = !this.styleState;
        },
    }
   
}
</script>

<style scoped>


.todo-tools{
    .todo-tools__item{
        margin-left: 8px;
    }
}

.todoDone{
    text-decoration: line-through;
    color: gray;
}

.todoDoneBck{
   opacity: 0.6;
}

.v-card{
    transition: all 1s;
}

.v-btn.edit:hover{
    background-color: #3641b3;
}

.v-btn.x:hover{
    background-color: red;
}
.v-btn.done:hover{
    background-color: #1B5E20;
}
@media (max-width: 460px){
    .mobile-spacer{
        padding: 0px 0px;
        margin: 0px 0px;
        font-size: 12px;
        width: 5px;
        height: 25px;
        
    }
}

</style>