<template>
    <ul>
     <TodoItem v-for="todo in todoList" v-bind:key="todo.id" :todo="todo" @remove-todo="removeTodo" @edit-todo="editTodo" />
    </ul>
</template>
<script>
import TodoItem from './TodoItem.vue'

export default {
    props:['todoList'],
    components: {
        TodoItem,
    },
    methods: {
        removeTodo(id){
            this.$emit('remove-todo', id);
        },
        editTodo(state, id){
            this.$emit('edit-todo', state, id)
           
        },
    }
    
}
</script>

<style>
ul{
    width: 100%;
}
</style>