<template>
    
            <div class="popup-body" v-if="visible">
            <h3 class="popup-title">Edytuj zadanie:</h3>
            <div class="popup-content">
                <v-text-field class="edit-input" type="text" v-model="editedTodo" @keyup.enter="editTodo" @keyup.escape="$emit('close-popup')"></v-text-field>
                
                <v-btn class="apply-button" @click="editTodo">Zatwierdź</v-btn>
                <v-btn class="close-button" @click="$emit('close-popup')">Anuluj</v-btn>
                <p class="errorLine" v-if="errorMsg">{{ errorMsg }}</p>
            </div>
        </div>
   

</template>

<script>
export default{
    props:
    {
        visible:{
            type: Boolean,
            required: true
        },

    },
    data(){
        return{
           editedTodo: '',
           errorMsg:'',
        }
    },
    methods: {
        editTodo(){
            if (this.editedTodo == ''){
                this.errorMsg='Wpisz coś !'
            } else{
                this.$emit('editing-todo', this.editedTodo);
                this.editedTodo = ''
                this.errorMsg = ''
            }
        }
    } 
      
    
}

</script>

<style scoped>
    .popup-body{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: fixed;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgb(0, 0, 0);
        opacity: 0.8;
        overflow: hidden;
        padding: 0;
        margin: 0;
        

        h3{
            font-size: 30px;
            font-weight: lighter;
        }

        .popup-content{
            display: flex;
            flex-direction: column;
            padding: 5em;
            background-color: gray;
            width: 50%;
            overflow: hidden;
        }

        .edit-input{
            font-size: 20px;
            color: black;
            width: 100%;
            margin-bottom: 2em;
            border-radius: 5px;
        }

        .apply-button{
            margin-bottom: 5px;
        }
        
        .apply-button:hover{
            background-color: #BA68C8;
        }

        .close-button:hover{
            background-color: #BA68C8;
        }

        .apply-button,
        .close-button{
            transition: all 0.7s;
        }

        .errorLine{
        color: red;
        font-size: 18px;
        }

        @media (max-width: 460px){
            .popup-content{
                width: 100%;
            }
}
    }
</style>