<template>
  <v-responsive class="pa-4">
    <v-app :theme="theme">
      <v-app-bar color="surface-variant">
        
        <v-app-bar-title class="d-flex justify-center">Zielsone's ToDo App</v-app-bar-title>
        

      
     
        </v-app-bar>

      

          <v-main>
            <v-container class="app-container">

            <AddTodo @add-todo="addTodo"/> <!--Nasłuchujemy na zdarzenie add-todo gdy usłyszymy odpalamy addTodo-->
            <v-divider></v-divider>
            <TodoList :todoList="todoList" @remove-todo="removeTodo" @edit-todo="editTodo"/>
            <EditTodo :visible="isPopupVisible" @close-popup="closePopup" @editing-todo="editingTodo"/>

            </v-container>
          </v-main>
   </v-app>
  </v-responsive>
</template>

<script>
import TodoList from './components/TodoList.vue';
import AddTodo from './components/AddTodo.vue';
import EditTodo from './components/EditTodo.vue';





export default{
  components: {
    TodoList,
    AddTodo,
    EditTodo
  },
  
  data(){
     return{
        todoList: [
            {text: 'Wypić kawę', completed: false, id: 1},
            {text: 'Zjeść śniadanie', completed: false, id: 2},
            {text: 'Umyć okna', completed: false, id: 3}

    ],
    isPopupVisible: false,
    todoId: ''
     }
},
methods: { 
  addTodo(text){
  const newTodo = { //przerabiamy tekst na obiekt, ktory dodamy do tablicy
    text,
    id: Math.random(),
    completed: false
  }
  this.todoList.push(newTodo);
  },
  removeTodo(id){
    this.todoList = this.todoList.filter(todo => todo.id !== id)
  },
  editTodo(state, id){
    this.isPopupVisible = true;
    this.todoId = id;
  },
  closePopup(){
    this.isPopupVisible = false
  },
  editingTodo(editTodo){
    const task = this.todoList.find(todo => todo.id === this.todoId);
    if (task) {
      task.text = editTodo;
    }
    this.isPopupVisible = false
    },
  }
  }



</script>

<style>

.app-container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 10em;
  text-align: center;
}

.v-divider{
  width: 80%;
  height: 2px;
  background-color: #000;
  padding: 1em;
}



</style>
